<template>
  <div class="conference_detail">
    <div v-if="!isProposalEmpty">
      <div class="schedule_block">
        <h3 class="schedule_block__title">四、議案與討論事項</h3>
      </div>
      <table class="detail_table">
        <tr class="case_info">
          <td class="title" width="25px">案次</td>
          <td class="title_data">第一案</td>
          <td class="title" width="50px">提案單位</td>
          <td class="title_data">{{proposal.dept}} {{proposal.name}}</td>
        </tr>
        <tr class="case_summary">
          <td class="title">案由</td>
          <td colspan="3" align="left">{{proposal.reason}}</td>
        </tr>
        <tr class="case_description">
          <td class="title">說明</td>
          <td colspan="3" align="left">
            <a v-for="(description,index) in proposal.description" :key="index" :href="description" target="_blank">{{description}}</a>
          </td>
        </tr>
        <tr>
          <td class="title">討論</td>
          <td colspan="3" align="left">{{proposal.discussion}}</td>
        </tr>
      </table>

      <div class="toggle_btns">
        <div v-if="isLeader" class="toggle_btns__leader" :class="{ is_open: showLeaderVoteWindow }" @click="toggleLeaderWindow">操作</div>
        <div class="toggle_btns__vote" :class="{ is_open: votingInfo.isVoting && showVoteWindow }" @click="toggleVoteWindow">投票</div>
        <div class="toggle_btns__vote" :class="{ is_open: showResultWindow }" @click="toggleResultWindow">結果</div>
      </div>
    </div>
    <div v-else>
      <p>查無此會議</p>
      <router-link :to="{name: 'home'}" class="btn" tag="span">回到首頁</router-link>
    </div>
    <LeaderVoteWindow v-if="isLeader" v-show="showLeaderVoteWindow"/>
    <VoteWindow v-if="votingInfo.isVoting && showVoteWindow"/>
    <VoteDetailWindow v-if="showResultWindow"/>
  </div>
</template>

<script>
import VoteWindow from '@/components/VoteWindow.vue'
import LeaderVoteWindow from '@/components/LeaderVoteWindow.vue'
import VoteDetailWindow from '@/components/VoteDetailWindow.vue'
import { getProposalData } from '@/api/proposal'
import { mapState, mapActions } from 'vuex'

export default {
  name: 'ConferenceDetail',
  components: {
    VoteWindow,
    LeaderVoteWindow,
    VoteDetailWindow
  },
  data () {
    return {
      proposal: {},
      showVoteWindow: false,
      showLeaderVoteWindow: false,
      showResultWindow: false
    }
  },
  computed: {
    ...mapState({
      isLeader: state => state.user.isLeader,
      votingInfo: 'votingInfo'
    }),
    isProposalEmpty () {
      return Object.keys(this.proposal).length === 0
    }
  },
  created: async function () {
    this.setLodingStatus(true)
    await this.getProposalDetail(this.$route.params.delibrationID, this.$route.params.proposalID)
    this.setLodingStatus(false)
    this.$socket.emit('entryVote', {
      proposalID: this.$route.params.proposalID
    })
  },
  methods: {
    ...mapActions({
      setErrorWindow: 'error/setErrorWindow',
      setLodingStatus: 'setLodingStatus'
    }),
    async getProposalDetail (dID, pID) {
      const response = await getProposalData(dID, pID)
      this.proposal = response.data
    },
    toggleLeaderWindow () {
      this.showLeaderVoteWindow = !this.showLeaderVoteWindow
      this.showVoteWindow = false
      this.showResultWindow = false
    },
    toggleVoteWindow () {
      if (this.votingInfo.isVoting) {
        this.showLeaderVoteWindow = false
        this.showVoteWindow = !this.showVoteWindow
        this.showResultWindow = false
      } else {
        this.setErrorWindow({ showError: true, errorType: 'cantVote' })
      }
    },
    toggleResultWindow () {
      if (!this.votingInfo.isVoting) {
        this.showLeaderVoteWindow = false
        this.showVoteWindow = false
        this.showResultWindow = !this.showResultWindow
      } else {
        this.setErrorWindow({ showError: true, errorType: 'cantShowResult' })
      }
    }
  }
}
</script>

<style lang="scss">
.conference_detail{
  padding-top: 20px;
  width: 100%;
  height: 100%;
}
.detail_table{
  border-color:#fff;
  border-collapse:collapse;
  margin-bottom: 130px;
  width: 100%;
  .case_summary{
    font-weight: 700;
  }
  .title{
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    &_data{
      background-color: $secondary;
      font-weight: 700;
    }
  }
  td{
    border: 5px solid #fff;
  }
  li{
    margin-bottom: 10px;
  }
}

.toggle_btns {
  user-select: none;
  z-index: 1;
  position: fixed;
  right: 10px;
  bottom: 10px;
  display: flex;
  div {
    cursor: pointer;
    // color: #fff;
    font-weight: 700;
    padding: 0 5px;
    height: 30px;
    border-radius: 7px;
    border: 3px solid #3b3838;
    margin: 0 5px 0 0;
    &.is_open {
      background-color: #808080;
    }
  }
}

</style>
